.main-section {
  padding: 20px 50px;
}

.breed-select {
  margin-bottom: 15px;
}

.cat-info-box {
  width: 654px;
}

.loader {
  width: 100%;
  height: 4.8px;
  background: rgba(255, 255, 255, .15);
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.loader:after {
  content: "";
  width: 96px;
  height: 4.8px;
  box-sizing: border-box;
  background: rgba(8, 170, 164, .27);
  animation: hitZak 1s linear infinite alternate;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes hitZak {
  0% {
    left: 0;
    transform: translateX(-1%);
  }

  100% {
    left: 100%;
    transform: translateX(-99%);
  }
}

